import React, { useState } from 'react';
import { Container, Typography } from '@mui/material';
import PegBoard from './PegBoard';
import Controls from './Controls';
import './App.css';

function App() {
  const [holes, setHoles] = useState(10); // default holes
  const [holeSize, setHoleSize] = useState(30); // default hole size in pixels
  const [horizontalGap, setHorizontalGap] = useState(5); // default horizontal gap
  const [verticalGap, setVerticalGap] = useState(5); // default vertical gap

  return (
    <Container maxWidth="sm" className="app-container">
      <Typography variant="h4" align="center" gutterBottom>
        Peg Board Creator
      </Typography>
      <Controls
        holes={holes}
        setHoles={setHoles}
        holeSize={holeSize}
        setHoleSize={setHoleSize}
        horizontalGap={horizontalGap}
        setHorizontalGap={setHorizontalGap}
        verticalGap={verticalGap}
        setVerticalGap={setVerticalGap}
      />
      <PegBoard holes={holes} holeSize={holeSize} horizontalGap={horizontalGap} verticalGap={verticalGap} />
    </Container>
  );
}

export default App;
