import React from 'react';
import { Box, Button } from '@mui/material';
import jsPDF from 'jspdf';

function PegBoard({ holes, holeSize, horizontalGap, verticalGap }) {
  const holesArray = Array.from({ length: holes }, (_, i) => i);

  // Function to generate PDF and trigger download
  const downloadPDF = () => {
    const pdf = new jsPDF('landscape', 'pt', 'a4');

    // Adding a white background to the PDF
    pdf.setFillColor(255, 255, 255);
    pdf.rect(0, 0, pdf.internal.pageSize.width, pdf.internal.pageSize.height, 'F');

    holesArray.forEach((_, index) => {
      const x = (index % 10) * (holeSize + horizontalGap) + 10; // 10 for padding
      const y = Math.floor(index / 10) * (holeSize + verticalGap) + 10; // 10 for padding
      pdf.setFillColor(200); // Light gray color for holes
      pdf.circle(x + holeSize / 2, y + holeSize / 2, holeSize / 2, 'F');
    });

    pdf.save('pegboard.pdf');
  };

  const boardStyle = {
    display: 'grid',
    gridTemplateColumns: `repeat(10, 1fr)`,
    gap: `${verticalGap}px ${horizontalGap}px`,
    width: 'calc(1122px - 20px)',
    height: 'calc(793px - 20px)',
    border: '1px solid #000',
    padding: '10mm',
    boxSizing: 'border-box',
  };

  return (
    <Box>
      <Box style={boardStyle}>
        {holesArray.map((hole) => (
          <Box key={hole} sx={{
            width: `${holeSize}px`,
            height: `${holeSize}px`,
            backgroundColor: 'lightgray',
            borderRadius: '50%',
          }} />
        ))}
      </Box>
      <Button variant="contained" onClick={downloadPDF} style={{ marginTop: '20px' }}>
        Save as PDF
      </Button>
    </Box>
  );
}

export default PegBoard;
