import React from 'react';
import { Box, Slider, Typography } from '@mui/material';

function Controls({ holes, setHoles, holeSize, setHoleSize, horizontalGap, setHorizontalGap, verticalGap, setVerticalGap }) {
  return (
    <Box display="flex" justifyContent="space-between" marginBottom={2}>
      <Box flexGrow={1}>
        <Typography gutterBottom>Number of Holes:</Typography>
        <Slider
          value={holes}
          onChange={(e, newValue) => setHoles(newValue)}
          min={1}
          max={100}
          valueLabelDisplay="auto"
        />
        <Typography gutterBottom>Hole Size (px):</Typography>
        <Slider
          value={holeSize}
          onChange={(e, newValue) => setHoleSize(newValue)}
          min={10}
          max={50}
          valueLabelDisplay="auto"
        />
        <Typography gutterBottom>Horizontal Gap (px):</Typography>
        <Slider
          value={horizontalGap}
          onChange={(e, newValue) => setHorizontalGap(newValue)}
          min={0}
          max={20}
          valueLabelDisplay="auto"
        />
        <Typography gutterBottom>Vertical Gap (px):</Typography>
        <Slider
          value={verticalGap}
          onChange={(e, newValue) => setVerticalGap(newValue)}
          min={0}
          max={20}
          valueLabelDisplay="auto"
        />
      </Box>
      <Box width="200px" paddingLeft={2}>
        <Typography variant="body1" align="justify">
          Create and customize your own peg board by adjusting hole sizes, number of holes, and spacing for your needs.
        </Typography>
      </Box>
    </Box>
  );
}

export default Controls;
